
@font-face {
	font-family: "Source Sans Pro 300";
	src: url('/static/fonts/SourceSansPro-Light.ttf');
}
body{
  font-family: "Source Sans Pro 300", serif; 
  margin: 0;
  padding: 0;
}
.content {
  padding-top: 50px;
}
.titlemessage h2
{
	font-size: 100px;
	margin:0;
}
.titlemessage 
{
	display: block;
	text-align: center;
	margin: auto;
	width: 700px;
}
.titlemessage p
{
	margin: 0;
	font-size: 46px;
	color: rgba( 0,0,0, 0.5);
	margin-bottom: 200px;
}
.cardList
{
	display: grid;
	grid-template-columns: auto;
	/*grid-template-columns: auto auto auto;
	grid-template-rows: auto auto auto;*/
	justify-items: center;
	grid-column-gap: 30px;
	grid-row-gap: 80px;
}
@media screen and (max-width: 600px) {
.profile.banner {
		height: 200px;
}
.profile:not(.banner) img {
		max-width: 100%;
		height: auto;
}
}
@media screen and (min-width: 1150px) {
.cardList {
		grid-template-columns: auto auto;
}
.profile.titlemessage{
		grid-row-start: 4;
		grid-column-end: 3;
}
.profile.banner {
		grid-row-start: 5;
		grid-column-end: 3;
		height: 600px;
}
}
@media screen and (min-width: 1720px) {
.cardList {
		grid-template-columns: auto auto auto;
}
.profile.titlemessage{
		grid-row-start: 3;
		grid-column-end: 4;
}
.profile.banner {
		grid-row-start: 4;
		grid-column-end: 4;
		height: 700px;
}
}

nav ul {
  display: inline;
  list-style-type: none;
  overflow: hidden;
  width: 100%;
}
nav {
  padding-top: 50px;
  padding-bottom: 50px;
  background-color: black;
}
nav li:not(.logo) {
  display: inline;
}
nav li a {
  font-size: 24px;
  padding-bottom: 0px;
  -webkit-transition: 0.5s;
  margin: 0px 10px;
  transition: 0.5s;
  color: white;
  text-align: center;
  text-decoration: none;
  font-family: "helvetica";
  font-variant: small-caps;
}
.logo {
  float: right;
  margin-right: 30px;
  margin-top: -40px;
}
.logo a img {
  height: 100px;
  width: auto;
}

.footer {
  display: block;
  background-color: #888;
  color: white;
  text-align: center;
  padding: 10px;
  margin-top: 100px;
}
.footer a {
  color: white;
  text-decoration: none;
}

.text {
	margin: auto;
	max-width: 1000px;
	font-size: 32px;
	text-align: justify;
	text-justify: inter-word;
}
.text img {
	max-width: 400px;
	height: auto;
	margin: 15px;
	margin-top: 0px;
	float: right;
}
.text img.left{
	float: left;
}

.profile.titlemessage{
	grid-row-start: 7;
	grid-column-start: 1;
	grid-column-end: 2;
}
.profile.titlemessage p {
margin-bottom: 0px;
}
.profile.banner {
	grid-row-start: 8;
	grid-column-start: 1;
	grid-column-end: 2;
	width: 100%;
	-webkit-transition: height 1s;
	transition: height 1s;
	height: 400px;
	background-position: center;
	background-size: cover;
	background-image: url('/static/images/45.png');
	width: 100%;
	display:block;
}
.profile:not(.banner) img {
	/*border-radius: 25px;*/
	width: 540px;
	-webkit-transition: width 1s;
	transition: width 1s;
	height: auto;
}
.profile:not(.banner):not(.titlemessage) {
	-webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	background-color: white;
}
.profile div {
	-webkit-margin-before: 0px;
	-webkit-margin-after: 20px;
	font-size: 64px;
	text-align: center;
}


label {
	font-size: 24px;
}
input:focus, textarea:focus {
	border: #555 solid 2px;
}
textarea {
	height: 200px;
}
textarea, input {
	width: 100%;
	font-size: 24px;
	outline: none;
	border: #CCC solid 2px;
	resize: none;
	-webkit-transition: 0.5s;
	transition: 0.5s;
	padding: 12px 20px;
	border-radius: 5px;
	margin: 10px 0px;
}
button {
	font-size: 24px;
	outline: none;
	padding: 12px 20px;
	border: none;
	width: 104%;

	border-radius: 5px;
	cursor: pointer;
	margin: 10px auto;
	color: white;
	-webkit-transition: 0.25s;
	transition: 0.25s;
	background-color: #00129B;
}
button:hover {
	/*background-color: #BF5D28;*/
}
#radioskill {
	display: grid;
	grid-template-columns: auto auto auto;
	grid-column-gap: 20px;
	height: 70px;
	-webkit-box-pack: center;
	    -ms-flex-pack: center;
	        justify-content: center;
	justify-items: center;
}
#radioskill label {
	float:left;
	width:200px;
	margin:4px;
	background-color:#EFEFEF;
	border-radius:4px;
	border:1px solid #D0D0D0;
	overflow:hidden;
}
#radioskill label span {
	text-align:center;
	font-size: 32px;
	-webkit-transition: 0.5s;
	transition: 0.5s;
	padding:13px 0px;
	display:block;
}
#radioskill label input {
  display: none;
}
#radioskill input:checked + span {
	background-color: #00129B;
	color:#F7F7F7;
}
.content.join {
	max-width: 1000px;
        margin-left: auto;
        margin-right: auto;
}

.card:not(.banner):not(.titlemessage) {
	-webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	background-color: white;
	width:560px;
}
.card div {
	-webkit-margin-before: 0px;
	-webkit-margin-after: 0px;
	font-size: 48px;
	text-align: center;
}
.card p {
	font-size: 24px;
	padding: 20px;
	color: rgba(0,0,0, 0.5);
}
